<template>
  <LoadingModal v-if="loading"/>
  <ConfirmModal
    v-if="confirm_flag"
    :confim_content = "confim_content"
    :params = "params"
    @confirm="confirm_flag = $event"
    @loading="loading = $event"
  />
  <form action="#" name="facet_admin_form" enctype="multipart/form-data">
    <div class="main_content">
      <div class="tab_wrapper">
        <div class="tab_container">
          <div id="serverTab" class="tab tab_active" @click="changeConfigType('server')">
            <h3 class="no_margin">サーバー</h3>
          </div>
          <div id="facetTab" class="tab tab_disable" @click="changeConfigType('facet')">
            <h3 class="no_margin">facetCloud</h3>
          </div>
        </div>
      </div>
      <div class="form_wrapper" v-if="config_type == 'server'">
        <div class="form_container">
          <h3>IP設定&emsp;</h3>
          <div class="form_box">
            <div class="ipconfig_container">
              <div class="ip_type_box" v-if="release_flag">
                <label for="ipManual"><input type="radio" id="ipManual" name="ip_type" value="manual" v-model="ip_type">静的IP</label>
                <label for="ipAuto"><input type="radio" id="ipAuto" name="ip_type" value="auto" v-model="ip_type">DHCP</label>
              </div>
              <table class="ip_table" v-if="ip_type == 'manual'">
                <tr class="ip_tr">
                  <th class="ip_title">IPアドレス(IPv4)：</th>
                  <td>
                    <div class="ip_container">
                      <input type="number" name="ip_1" class="ip_contents" :value="ipAddresses[0]" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="ip_2" class="ip_contents" :value="ipAddresses[1]" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="ip_3" class="ip_contents" :value="ipAddresses[2]" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="ip_4" class="ip_contents" :value="ipAddresses[3]" @input="allowNum">
                    </div>
                  </td>
                </tr>
                <tr class="ip_tr">
                  <th class="ip_title">サブネットマスク：</th>
                  <td>
                    <div class="ip_container">
                      <select name="subnet" class="select_subnet" :value="subnet">
                        <option value=""></option>
                        <option v-for="sn in subnetList" :value="sn.value" v-bind:key="sn.id">{{ sn.ip }}</option>
                      </select>
                    </div>
                  </td>
                </tr>
                <tr class="ip_tr">
                  <th class="ip_title">デフォルトゲートウェイ：</th>
                  <td>
                    <div class="ip_container">
                      <input type="number" name="gw_1" class="ip_contents" :value="gateway[0]" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="gw_2" class="ip_contents" :value="gateway[1]" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="gw_3" class="ip_contents" :value="gateway[2]" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="gw_4" class="ip_contents" :value="gateway[3]" @input="allowNum">
                    </div>
                  </td>
                </tr>
                <tr class="ip_tr">
                  <th class="ip_title">DNS：</th>
                  <td>
                    <div class="ip_container">
                      <input type="number" name="dns_1" class="ip_contents" :value="dns[0]" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="dns_2" class="ip_contents" :value="dns[1]" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="dns_3" class="ip_contents" :value="dns[2]" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="dns_4" class="ip_contents" :value="dns[3]" @input="allowNum">
                    </div>
                  </td>
                </tr>
              </table>
            </div>
            <div>
              <div class="confirm_btn" id="ip" @click="confirm">更新</div>
            </div>
          </div>
        </div>
        <hr size="10">
        <div class="form_container">
          <h3>NTP</h3>
          <div class="form_box">
            <div class="ntp_box">
              <div class="ntp_datetime">
                <p id="system_datetime">0000/00/00 00:00:00</p>
                <div class="btn btn_sync" @click="syncDatetime()">PC時刻と同期</div>
              </div>
              <div class="ntp_config">
                <label for="ntp_form">同期先：<input type="text" name="ntp" id="ntp_form" :value="ntp"></label>
              </div>
            </div>
            <div>
              <div class="confirm_btn" id="ntp" @click="confirm">更新</div>
            </div>
          </div>
        </div>
        <hr size="10">
        <div class="form_container">
          <h3>SSL設定</h3>
          <div class="form_box">
            <div>
              <div>
                <p class="no_margin form_info">SSL設定を変更します。通信方法が変更されるため、変更時はご注意ください。</p>
                <p class="no_margin form_info">変更をする場合は確認画面をよくご確認の上実行してください。</p>
              </div>
              <div class="ssl_container">
                <label for="sslDisable"><input type="radio" id="sslDisable" name="ssl_enable" value="0" v-model="ssl_enable">無効</label>
                <label for="sslEnable"><input type="radio" id="sslEnable" name="ssl_enable" value="1" v-model="ssl_enable">有効</label>
              </div>
            </div>
            <div>
              <div class="confirm_btn" id="sslEnable" @click="confirm">実行</div>
            </div>
          </div>
        </div>
        <hr size="10" v-if="ssl.ssl_enable">
        <div class="form_container" v-if="ssl.ssl_enable">
          <h3>SSL証明書更新</h3>
          <div class="form_box">
            <div class="ssl_text">
              <div>現在のSSL証明書は「{{ ssl.expiry }}」が有効期限です。</div>
              <div>※更新実行後、UI操作が数分間操作不可となりますのでご注意ください。</div>
            </div>
            <div>
              <div class="confirm_btn" id="ssl" @click="confirm">更新</div>
            </div>
          </div>
        </div>
        <hr size="10">
        <div class="form_container">
          <h3>ミドルウェアアップデート</h3>
          <div class="form_box">
            <div class="middleware_container">
              <input type="file" name="middleware_file">
            </div>
            <div>
              <div class="confirm_btn" id="middleware" @click="confirm">更新</div>
            </div>
          </div>
        </div>
        <hr size="10">
        <div class="form_container">
          <h3>手動再起動</h3>
          <div class="form_box">
            <div>
              <p class="no_margin form_info">サーバーを再起動します。</p>
              <p class="no_margin form_info">再起動時は全ての処理が中断され接続ができません。</p>
            </div>
            <div>
              <div class="confirm_btn" id="rebootM" @click="confirm">実行</div>
            </div>
          </div>
        </div>
        <hr size="10">
        <div class="form_container">
          <h3>自動再起動</h3>
          <div class="form_box">
            <div>
              <div>
                <p class="no_margin form_info">指定のスケジュールで自動でサーバーを再起動します。</p>
                <p class="no_margin form_info">再起動時は全ての処理が中断され接続ができません。</p>
              </div>
              <div class="reboot_container">
                <div class="reboot_enable">
                  <label for="rebootDisable"><input type="radio" id="rebootDisable" name="reboot_enable" value="disable" v-model="reboot_type">無効</label>
                  <label for="rebootEnable"><input type="radio" id="rebootEnable" name="reboot_enable" value="enable"  v-model="reboot_type">有効</label>
                </div>
                <label v-if="reboot_type == 'enable'" class="reboot_week" for="rebootWeek">曜日
                  <select name="reboot_week" id="rebootWeek" v-model="reboot_week">
                    <option v-for="week in weekArray" :value="week.value" v-bind:key="week.id">{{ week.name }}</option>
                  </select>
                </label>
                <label v-if="reboot_type == 'enable'" class="reboot_hour" for="rebootHour">時間
                  <input type="number" id="rebootHour" placeholder="0" name="reboot_hour" v-model="reboot_hour" @input="rebootHourControl">時
                </label>
              </div>
            </div>
            <div>
              <div class="confirm_btn" id="rebootA" @click="confirm">実行</div>
            </div>
          </div>
        </div>
        <hr size="10">
      </div>
      <div class="form_wrapper" v-if="config_type == 'facet'">
        <div class="form_container">
          <h3>facetCloudバージョンアップ</h3>
          <div class="form_box">
            <div class="facetUp_container">
              <input type="file" name="facet_file">
            </div>
            <div>
              <div class="confirm_btn" id="facetUp" @click="confirm">更新</div>
            </div>
          </div>
        </div>
        <hr size="10">
        <div class="form_container">
          <h3>ログ取得</h3>
          <div class="form_box">
            <div>
              <p class="no_margin form_info">指定があった場合にのみ取得を行ってください。</p>
            </div>
            <div>
              <div class="confirm_btn" id="facetLog" @click="confirm">取得</div>
            </div>
          </div>
        </div>
        <hr size="10">
        <div class="form_container">
          <h3>入出力処理の排他制御強制解除</h3>
          <div class="form_box">
            <div>
              <p class="no_margin form_info">エクスポートやインポートの処理中に実行すると処理が失敗します。</p>
              <p class="no_margin form_info">必ず必要な場合にのみ実行を行ってください。</p>
            </div>
            <div>
              <div class="confirm_btn" id="exclusive" @click="confirm">実行</div>
            </div>
          </div>
        </div>
        <hr size="10">
      </div>
    </div>
  </form>
</template>

<script>
const axios = require('axios')
import LoadingModal from '@/components/LoadingModal.vue'
import ConfirmModal from '@/components/ConfirmModal.vue';

export default {
  name: 'TopPage',
  components: {
    LoadingModal,
    ConfirmModal
  },
  data () {
    return {
      release_flag: false,
      config_type: 'server',
      ip_type: 'manual',
      subnetList: [
        {id: "1", value:"8", ip:"8 (255.0.0.0)"},
        {id: "2", value:"9", ip:"9 (255.128.0.0)"},
        {id: "3", value:"10", ip:"10 (255.192.0.0)"},
        {id: "4", value:"11", ip:"11 (255.224.0.0)"},
        {id: "5", value:"12", ip:"12 (255.240.0.0)"},
        {id: "6", value:"13", ip:"13 (255.248.0.0)"},
        {id: "7", value:"14", ip:"14 (255.252.0.0)"},
        {id: "8", value:"15", ip:"15 (255.254.0.0)"},
        {id: "9", value:"16", ip:"16 (255.255.0.0)"},
        {id: "10", value:"17", ip:"17 (255.255.128.0"},
        {id: "11", value:"18", ip:"18 (255.255.192.0)"},
        {id: "12", value:"19", ip:"19 (255.255.224.0)"},
        {id: "13", value:"20", ip:"20 (255.255.240.0)"},
        {id: "14", value:"21", ip:"21 (255.255.248.0)"},
        {id: "15", value:"22", ip:"22 (255.255.252.0)"},
        {id: "16", value:"23", ip:"23 (255.255.254.0)"},
        {id: "17", value:"24", ip:"24 (255.255.255.0)"},
        {id: "18", value:"25", ip:"25 (255.255.255.128)"},
        {id: "19", value:"26", ip:"26 (255.255.255.192)"},
        {id: "20", value:"27", ip:"27 (255.255.255.224)"},
        {id: "21", value:"28", ip:"28 (255.255.255.240)"},
        {id: "22", value:"29", ip:"29 (255.255.255.248)"},
        {id: "23", value:"30", ip:"30 (255.255.255.252)"},
        {id: "24", value:"31", ip:"31 (255.255.255.254)"},
        {id: "25", value:"32", ip:"32 (255.255.255.255)"},
      ],
      reboot_type: 'disable',
      reboot_week: 'sun',
      reboot_hour: '0',
      weekArray: [
        {id: "1", value:"sun", name:"日曜日"},
        {id: "2", value:"mon", name:"月曜日"},
        {id: "3", value:"tue", name:"火曜日"},
        {id: "4", value:"wed", name:"水曜日"},
        {id: "5", value:"thu", name:"木曜日"},
        {id: "6", value:"fri", name:"金曜日"},
        {id: "7", value:"sat", name:"土曜日"},
        {id: "8", value:"every", name:"毎日"},
      ],
      ipAddresses: ['','','',''],
      subnet: '',
      gateway: ['','','',''],
      dns: ['','','',''],
      ntp: '',
      ssl: '',
      ssl_enable: '0',
      params: '',
      confim_content: '',
      confirm_flag: false,
      loading: false
    }
  },
  mounted () {
    let self = this
    // IP設定の取得・反映
    axios
      .get(location.origin + '/server/backend/system/ipconfig')
      .then(function (response) {
        self.ipAddresses = response.data.address.split('.')
        self.subnet = response.data.subnet
        self.gateway = (response.data.gateway) ? response.data.gateway.split('.') : ['','','','']
        self.dns = (response.data.dns) ? response.data.dns.split('.') : ['','','','']
      })
    // 自動再起動設定の取得・反映
    axios
      .get(location.origin + '/server/backend/system/rebootSchedule')
      .then(function (response) {
        // タイプの変更
        self.reboot_type = response.data.reboot
        if (response.data.reboot === 'enable') {
          // 有効の場合は設定を反映
          self.reboot_week = (response.data.week === '*') ? 'every': response.data.week
          self.reboot_hour = response.data.hour
        }
      })
    // NTP設定の取得・反映
    axios
      .get(location.origin + '/server/backend/system/ntp')
      .then(function (response) {
        self.ntp = response.data.ntp
      })
    // SSL設定の取得・反映
    axios
      .get(location.origin + '/server/backend/system/ssl')
      .then(function (response) {
        self.ssl = response.data
        self.ssl_enable = (self.ssl.ssl_enable) ? '1' : '0'

      })
    
    // 時刻同期用
    setInterval(function () {
      if (self.config_type == 'server') {
        axios
          .get(location.origin + '/server/backend/system/datetime')
          .then(function (response) {
            document.getElementById('system_datetime').textContent = response.data.datetime
          })
      }
    }, 1000)
  },
  methods: {
    // IP設定の入力時
    allowNum: function (e) {
      // ドットが入力されたら次のフォームへフォーカス
      if (e.data == '.' && e.target.name.split('_')[1] != 4) {
        let next_class = e.target.name.split('_')[0]+'_'+(Number(e.target.name.split('_')[1])+1)
        document.getElementsByName(next_class)[0].focus()
      }

      let input_len = e.target.value.length
      // 3桁制限
      e.target.value = e.target.value.slice(0, 3)

      // 3桁入力されたら次のフォームへフォーカス
      if (input_len == 3) {
        if (e.target.name.split('_')[1] != 4) {
          let next_class = e.target.name.split('_')[0]+'_'+(Number(e.target.name.split('_')[1])+1)
          document.getElementsByName(next_class)[0].focus()
        }
      }
    },
    syncDatetime: function () {
      let dt = new Date();
      let local = dt.toLocaleString();
      axios
      .post(location.origin + '/server/backend/system/datetime', {'datetime': local})
      .then(function (response) {
        document.getElementById('system_datetime').textContent = response.data.datetime
      })
      .catch(function (e) {
        alert(e.response.data.msg)
      })
    },
    confirm: function (e) {
      let self = this

      self.confim_content = e.target.id

      switch (e.target.id) {
        case 'ip':
          self.params = self.ipUpdate()
          break;
        case 'ntp':
          self.params = self.ntpUpdate()
          break;
        case 'ssl':
          self.params = true
          break;
        case 'sslEnable':
          self.params = self.sslEnable()
          break;
        case 'middleware':
          self.params = self.updateMiddleware()
          break;
        case 'rebootM':
          self.params = true
          break;
        case 'rebootA':
          self.params = self.execRebootSchedule()
          break;
        case 'facetUp':
          self.params = self.updateFacet()
          break;
        case 'facetLog':
          self.params = true
          break;
        case 'exclusive':
          self.params = true
          break;
      
        default:
          break;
      }

      if (self.params) {
        self.confirm_flag = true
      }

    },
    ipUpdate: function () {
      let self = this
      const form = document.facet_admin_form;
      
      if (!this.release_flag || form.ip_type.value === 'manual') {
        let ip = [form.ip_1.value, form.ip_2.value, form.ip_3.value, form.ip_4.value]
        let gw = [form.gw_1.value, form.gw_2.value, form.gw_3.value, form.gw_4.value]
        let dns = [form.dns_1.value, form.dns_2.value, form.dns_3.value, form.dns_4.value]
        
        // 入力バリデーション
        if (ip.filter((i) => i !== '').length === 0) {
          alert('【IP設定「IPアドレス(IPv4)」】必須項目です')
          return false
        } else if (ip.filter((i) => i !== '').length !== 4) {
          alert('【IP設定「IPアドレス(IPv4)」】4つすべて入力してください')
          return false
        }
        if (form.subnet.value == "") {
          alert('【IP設定「サブネットマスク」】必須項目です')
          return false
        }
        if (gw.filter((g) => g !== '').length === 0) {
          gw = []
        } else if (gw.filter((g) => g !== '').length !== 4) {
          alert('【IP設定「Gateway」】未入力もしくは4つすべて入力してください')
          return false
        }
        if (dns.filter((d) => d !== '').length === 0) {
          dns = []
        } else if (dns.filter((d) => d !== '').length !== 4) {
          alert('【I設定「DNS」】未入力もしくは4つすべて入力してください')
          return false
        }

        // 問題ない場合はthis変数へ
        self.ipAddresses = ip
        self.subnet = form.subnet.value
        self.gateway = gw
        self.dns = dns

        return {
          'ip_type': 'manual',
          'ip': ip,
          'subnet': form.subnet.value,
          'gw': gw,
          'dns': dns
        }
      } else {
        return {'ip_type': 'auto'}
      }
    },
    ntpUpdate: function () {
      let self = this
      const form = document.facet_admin_form;

      if (form.ntp.value == '') {
        // 空欄ではないかの確認
        alert('【NTP】必須項目です')
        return false
      } else if (form.ntp.value == self.ntp) {
        // 変更の有無確認
        alert('【NTP】現在の同期先から変更を行ってください')
        return false
      } else {
        return {'ntp':form.ntp.value}
      }

    },
    sslEnable: function () {
      let self = this
      if (self.ssl.ssl_enable == self.ssl_enable) {
        alert('【SSL設定】現在の設定から変更されていません')
        return false
      } else {
        return {'ssl_enable': self.ssl_enable}
      }
    },
    rebootHourControl: function (e) {
      if (e.target.value > 23) {
        e.target.value = 23
      } else if (e.target.value < 0) {
        e.target.value = 0
      }
    },
    // タブ操作時
    changeConfigType: function (type) {
      // 設定種類の変更
      this.config_type = type
      // タブのクラスの変更
      const targetIdname = type+'Tab';
      let tabElements = document.getElementsByClassName('tab')
      for (let i = 0; i < tabElements.length; i++) {
        if (tabElements[i].id !== targetIdname) {
          tabElements[i].classList.add('tab_disable')
          tabElements[i].classList.remove('tab_active')
        } else {
          tabElements[i].classList.remove('tab_disable')
          tabElements[i].classList.add('tab_active')
        }
      }
    },
    updateMiddleware: function () {

      const params = new FormData();
      params.append("middleware_file", document.facet_admin_form.middleware_file.files[0]);

      return params
    },
    execRebootSchedule: function () {
      let form = document.facet_admin_form

      // 送信パラメータ格納
      let params;
      if (form.reboot_enable.value === 'enable') {
        params = {
          'reboot_enable': form.reboot_enable.value,
          'reboot_week': form.reboot_week.value,
          'reboot_hour': (form.reboot_hour.value) ? form.reboot_hour.value : 0,
        }
      } else {
        params = {
          'reboot_enable': form.reboot_enable.value
        }
      }

      return params

    },
    updateFacet: function () {
      const params = new FormData();
      params.append("facet_file", document.facet_admin_form.facet_file.files[0]);

      return params
    },
    confirmPermission: function () {
      return document.getElementById('basicLoginUser').textContent
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h2, h3 {
  margin: 0;
  text-align: start;
}

input, select {
  padding: 0.5rem 1rem;
  border-radius: 10px;
}

/* input, select, label {
  font-size: 1.2rem;
} */

select {
  width: fit-content;
}

input[type=text] {
  box-sizing: border-box;
  border-radius: 5px;
  padding: 0.3rem 1rem;
  font-size: 1.1rem;
  width: 200px;
}
input[type=checkbox] {
  height: 30px;
  width: 30px;
  cursor: pointer;
}
input[type=datetime-local] {
  box-sizing: border-box;
}

textarea {
  resize: none;
}

.relative {
  position: relative;
}

.main_content {
  background-color: #fff;
  box-sizing: border-box;
  padding: 1rem 100px;
  width: 100%;
}

.form_wrapper {
  padding: 2rem;
  box-sizing: border-box;
  border: 2px solid #003da6;
}

.form_container {
  width: 100%;
  margin: 1.5rem 0 3rem 0;
}
.form_container:first-child {
  margin: 0 0 3rem 0;
}

.check_container {
