<template>
  <div class="confirm_wrapper">
    <div class="confirm_container">
      <h1>確認内容</h1>
      <div class="confirm_contents">
        <h2>項目「{{ confirm_list[confim_content] }}」</h2>
        <div class="confirm_text" v-html="confirmText(confim_content)">
        </div>
        <div class="confirm_btn_box">
          <div class="btn submit_btn" @click="exec(confim_content)">実行</div>
          <div class="btn back_btn" @click="confirm_back()">戻る</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const axios = require('axios')
import { saveAs } from 'file-saver'

export default {
  data () {
    return {
      confirm_list: {
        'ip':'IP設定',
        'ntp':'NTP',
        'sslEnable':'SSL設定',
        'ssl':'SSL証明書更新',
        'middleware':'ミドルウェアアップデート',
        'rebootM':'手動再起動',
        'rebootA':'自動再起動',
        'facetUp':'facetCloudバージョンアップ',
        'facetLog':'ログ取得',
        'exclusive':'排他制御強制解除'
      },
      week_list: {
        "sun":"日曜日",
        "mon":"月曜日",
        "tue":"火曜日",
        "wed":"水曜日",
        "thu":"木曜日",
        "fri":"金曜日",
        "sat":"土曜日",
        "every":"毎日"
      }
    }
  },
  props: {
    confim_content: String,
    params: Array
  },
  methods: {
    confirm_back: function () {
      this.$emit('confirm', false)
    },
    exec: function (type) {
      this.$emit('loading', true)

      let self = this

      switch (type) {
        case 'ip':
          axios
          .post(location.origin + '/server/backend/system/ipconfig', self.params)
          .then(function () {
            alert("まもなくサーバーの再起動がされます...");

            // IP適用のための再起動
            axios
              .post(location.origin + '/server/backend/system/reboot/manual', "")
              .then(function () {
                console.log("start reboot")
                location.href = 'http://'+self.params.ip.join('.')+'/server/user/'
              })
          })
          .catch(function (e) {
            self.$emit('loading', false)
            alert(e.response.data.msg)
          })
          break;
        case 'ntp':
          axios
            .post(location.origin + '/server/backend/system/ntp', self.params)
            .then(function (response) {
              console.log(response.data)
              alert('実行が終了しました')
              location.reload()
            })
            .catch(function (e) {
              self.$emit('loading', false)
              alert(e.response.data.msg)
            })
          break;
        case 'ssl':
          axios
            .post(location.origin + '/server/backend/system/ssl', '')
            .then(function (response) {
              console.log(response.data)
              alert('実行が終了しました')
              location.reload()
            })
            .catch(function (e) {
              self.$emit('loading', false)
              alert(e.response.data.msg)
            })
          break;
        case 'sslEnable':
          axios
            .post(location.origin + '/server/backend/system/sslSwitch', self.params)
            .then(function (response) {
              console.log(response.data)
              let protocol = (self.params.ssl_enable === "1") ? 'https' : 'http'
              alert('変更が終了しました。まもなく再起動がされます。')
              axios
              .post(location.origin + '/server/backend/system/reboot/manual', "")
              .then(function () {
                location.href = protocol + '://' + location.host + '/server/user/'
              })
            })
            .catch(function (e) {
              self.$emit('loading', false)
              alert(e.response.data.msg)
            })
          break;
        case 'middleware':
          if (document.facet_admin_form.middleware_file.files[0]) {
            axios
              .post(location.origin + '/server/backend/system/middleware', self.params, {
                headers: {
                  "Content-Type": "multipart/form-data"
                }
              })
              .then(function (response) {
                console.log(response.data)
                location.reload();
              })
              .catch(function (e) {
                self.$emit('loading', false)
                alert(e.response.data.msg)
              })
            
          } else {
            alert("【ミドルウェアアップデート】アップロードファイルが見つかりません")
          }
          break;
        case 'rebootM':
          axios
            .post(location.origin + '/server/backend/system/reboot/manual', "")
            .then(function (response) {
              console.log(response.data)
              alert('再起動を実施しました')
              location.reload()
            })
            .catch(function (e) {
              self.$emit('loading', false)
              alert(e.response.data.msg)
            })
          break;
        case 'rebootA':
          axios
            .post(location.origin + '/server/backend/system/reboot/auto', self.params)
            .then(function (response) {
              console.log(response.data)
              alert('実行が終了しました')
              location.reload()
            })
            .catch(function (e) {
              self.$emit('loading', false)
              alert(e.response.data.msg)
            })
          break;
        case 'facetUp':
          if (document.facet_admin_form.facet_file.files[0]) {
            axios
              .post(location.origin + '/server/backend/facet/versionUpdate', self.params, {
                headers: {
                  "Content-Type": "multipart/form-data"
                }
              })
              .then(function (response) {
                console.log(response.data)
                location.reload();
              })
              .catch(function (e) {
                self.$emit('loading', false)
                alert(e.response.data.msg)
              })
            
          } else {
            alert("【facetCloudバージョンアップ】アップロードファイルが見つかりません")
          }
          break;
        case 'facetLog':
          axios
            .get(location.origin + '/server/backend/facet/facetLog', {
              responseType: 'blob'
            })
            .then(function (response) {
              let mineType = response.headers['content-type']
              let now = new Date().toLocaleDateString('ja-JP', {year:'numeric',month:'2-digit',day:'2-digit',hour:'2-digit',minute:'2-digit',second:'2-digit'}).replaceAll('/','').replaceAll(':','').replace(' ','')
              const name = 'logs_'+now+'.tar.gz'
              const blob = new Blob([response.data], {type: mineType})
              saveAs(blob, name)

              setTimeout(function () {
                location.reload()
              }, 5000)
            })
            .catch(function (e) {
              alert(e.response.data.msg)
              self.$emit('loading', false)
            })
          break;
        case 'exclusive':
          axios
            .post(location.origin + '/server/backend/facet/exclusiveCancel', '')
            .then(function (response) {
              console.log(response.data)
              alert('実行が終了しました')
              location.reload()
            })
            .catch(function (e) {
              alert(e.response.data.msg)
              self.$emit('loading', false)
            })
          break;
      
        default:
          break;
      }
    },
    confirmText: function (type) {
      let self = this
      let text = []
      switch (type) {
        case 'ip':
          if (self.params.ip_type == 'manual') {
            text = [
              '以下の内容で登録を行います。',
              '＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝',
              'IPアドレス：'+self.params.ip.join('.')+'/'+self.params.subnet,
              'GateWay：'+self.params.gw.join('.'),
              'DNS：'+self.params.dns.join('.'),
              '＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝',
              '',
              '実行後はサーバーが再起動されます。',
              'facetCloud内で実行している処理がある場合は処理が完了してから実行してください。',
              'IP変更後はFaceFC側の接続設定情報も変更する必要があります。',
              '',
              '再起動実行後、サーバーが起動するまでお待ちいただき、暫くしてからブラウザの更新を行ってください。'
            ]
          } else {
            text = [
              '以下の内容で登録を行います。',
              '＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝',
              'IP：DHCP設定（自動設定）',
              '＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝',
              '※DHCP設定の場合IPの特定ができないためドメイン利用以外の場合はご注意ください。',
              '',
              '実行後はサーバーが再起動されます。',
              'facetCloud内で実行している処理がある場合は処理が完了してから実行してください。',
              '',
              '再起動実行後、サーバーが起動するまでお待ちいただき、暫くしてからブラウザの更新を行ってください。'
            ]
          }
          break;
        case 'ntp':
          text = [
            '以下の内容で登録を行います。',
            '＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝',
            'NTP同期先：'+self.params.ntp,
            '＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝'
          ]
          break;
        case 'ssl':
          text = [
            'SSL証明書の更新を行います。',
            '',
            'NTPの同期をするためにデフォルトゲートウェイを設定してください。',
            '',
            '更新が実行されるとUI操作などが数分間使用できなくなるため、実行後はしばらくお待ちください。',
            '※使用の可否確認としてログイン画面などのUI画面が開けるかどうかで確認可能です。'
          ]
          break;
        case 'sslEnable':
          text = [
            'SSL設定を以下の様に変更します。',
            '＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝',
            'SSL設定：'+(self.params.ssl_enable === "1") ? "有効" : "無効",
            '＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝',
            '',
            '下記注意事項をご確認の上設定変更を実行してください。',
            '＊変更後、再起動を行います。再起動中は全ての処理が中断されるため、処理を実行している場合は完了後に実行してください。',
            '＊変更後はアクセスするURLのプロトコルが変更されますのでFaceFC側の設定や操作画面のURLを変更してください。',
            '　・facetCloud関連の操作画面やAPIについて、無効の場合は「http」、有効の場合は「https」となります。',
            '　・FaceFC側のWebSocket登録について、無効の場合は「ws」、有効の場合は「wss」となります。',
            '',
            'その他注意点などの詳細はマニュアルをご参照ください。'
          ]
          break;
        case 'middleware':
          text = [
            '実行するとミドルウェアが再起動されます。',
            '再起動されるとfacetCloudとFaceFCとの通信ができなくなるため、処理を実行している場合は完了後に実行してください。',
            '',
            '再起動実行後、サーバーが起動するまでお待ちいただき、暫くしてから通常通りご利用ください。'
          ]
          break;
        case 'rebootM':
          text = [
            '実行するとサーバーが再起動されます。',
            '再起動されるとfacetCloud内で実行されている処理も中断されますのでご注意ください。',
            '',
            '再起動実行後、サーバーが起動するまでお待ちいただき、暫くしてからブラウザの更新を行ってください。'
          ]
          break;
        case 'rebootA':
          if (self.params.reboot_enable == 'enable') {
            text = [
              '以下の内容で更新を行います。',
              '＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝',
              '自動再起動：有効',
              '再起動周期：'+self.week_list[self.params.reboot_week],
              '再起動時間：'+self.params.reboot_hour+'時',
              '＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝',
              '',
              '再起動されるとfacetCloud内で実行されている処理も中断されますので指定する日時にご注意ください。'
            ]
          } else {
            text = [
              '以下の内容で更新を行います。',
              '＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝',
              '自動再起動：無効',
              '＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝',
              '',
              '更新成功すると自動での再起動をしなくなります。'
            ]

          }
          break;
        case 'facetUp':
          text = [
            'アップデートを行うとfacetCloudでの処理が上手くいかなくなる可能性があるため、処理を実行している場合は完了後に実行してください。',
            '',
            'アップデート実行後、facetCloud画面上で再読み込みしてから更新通常通りご利用ください。'
          ]
          break;
        case 'facetLog':
          text = [
            '指示があった場合等の必要な時以外は取得を行わないでください。',
            '',
            'facetCloudで出力されるログが多い場合は時間がかかる場合があります。',
            'できるだけfacetCloud内の負荷がかかっていない時間帯に実施をしてください。'
          ]
          break;
        case 'exclusive':
          text = [
            '指示があった場合等の必要な時以外は実行しないでください。',
            'facetCloudでエクスポートや一括登録を行っている際に実行すると、処理が中断され不整合が発生する可能性があります。'
          ]
          break;
        default:
          break;
      }
      return text.join('<br>')
    }
  }
}
</script>

<style scoped>

.confirm_wrapper {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99;
}

.confirm_container {
  width: 60%;
  height: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  box-shadow: 0 0 10px #000;
  background-color: #fff;
  padding: 3rem;
}

.confirm_contents {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.confirm_btn_box {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 300px;
  margin-top: 2rem;
}

.btn.back_btn {
  background-color: gray;
}

.confirm_text {
  text-align: start;
}

.text_red {
  color: red;
}

.text_bold {
  font-weight: bold;
}

.small {
  font-size: 0.8rem;
}

.large {
  font-size: 1.2rem;
}

</style>
